import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DemoActionComponent } from './action-view.component';

const routes: Routes = [
    { path: '', component: DemoActionComponent },
    { path: '**', redirectTo: '' }
];

@NgModule({
    exports: [RouterModule],
    imports: [RouterModule.forChild(routes)]
})
export class DemoActionRoutingModule {}
